<!--饮水入口-->
<template>
    <s-header :name="'手动录入'" :rightclick="rightclick"/>
    <div class="div_content" :style="{ display:'flex', flexDirection: 'column'}">
        <div class="div_drinkadd_item">
            <span class="span_drinkadd_title">忘记饮水时间</span>

            <van-switch v-model="checked" size="16px"  active-color="#15AC78" inactive-color="#dcdee0"/>
        </div>
        <div class="div_drinkadd_item" @click="onClickSelectTime">
            <span class="span_drinkadd_title">饮水时间</span>
            <span>
              <span :style="{ marginRight: '5px' }" class="span_drinkadd_title">{{ drinkTime }}</span>
              <van-icon name="arrow" />
            </span>
        </div>

        <div style="margin-top: 10px;background-color: #FFFFFF;padding-bottom: 10px;flex: 1">
            <div class="div_layout_H_center" style="border-bottom: 0px;padding-top: 15px">
                <span class="span_drinkadd_title" :style="{ fontSize: '16px', color: '#1793F7' }">饮水量{{ drinkValue }}ml</span>
            </div>
            <div class="div_drinkadd_item" style="margin-top: 10px;border-bottom: 0px;height: 58px">
                <div class="div_drinkadd_drinknum_select" v-for="(item,index) in selectDate" :key="item" @click="onClickSelect(index)">
                    <img src="@/assets/ic_drink.png">
                    <span>{{item}}ml</span>
                </div>
            </div>

            <div class="div_drinkadd_item" style="border-bottom: 0px">
                <span class="span_drinkadd_title">自定义饮水量</span>
            </div>

           <div>
               <van-picker
                   :style="{ width: '50%',marginLeft: '25%'}"
                   :show-toolbar="false"
                   :columns="columns"
                   default-index="5"
                   item-height="25"
                   visible-item-count="5"
                   @change="onChangeDrinkPicker"
               />

           </div>

          <div class="btn_drink_bottom_div" @click="onCommitResult">
            保存
          </div>
          <!--时间选择器弹层-->

          <van-popup :show="popup1show" position="bottom" >
            <van-datetime-picker
                item-height="25"
                v-model="currentDate"
                type="datetime"
                :min-date="minDate"
                :max-date="maxDate"
                @cancel="cancleDatePicker"
                @confirm="confirmDatePicker"
                :formatter="formatter"
            />
          </van-popup>
        </div>


    </div>
</template>

<script>
  import sHeader from '@/components/SimpleHeader'
  import {reactive, onMounted, ref,toRefs} from 'vue'
  import moment from 'moment'
  import {getDayDrinkDetial, insertRecord} from "@/service/drink";
  import { useRouter } from "vue-router";
  import { getUserId } from  '@/utils/util'

  export default {
    name: "DrinkAdd",
    components: {
      sHeader,
    },
    setup() {
      const route = useRouter()
      onMounted(
        () => {
          console.log("当前分钟",new Date().getFullYear(), new Date().getMonth(), new Date().getDate(),
              new Date().getHours(),new Date().getMinutes())
          state.columns = getDrinkLineArr()
        }
      )
      /**
       * 保存饮水记录结果
       */
      const onCommitResult = async() => {
        console.log("最终饮水量", state.drinkTime, state.drinkValue)
        const res = await insertRecord(state.drinkValue,'ML', moment(new Date(state.drinkTime)).format("YYYY-MM-DD HH:mm:ss"),checked.value ? 1: 0,
            getUserId(), moment(state.drinkTime).format("YYYY-MM-DD"))
        const { code } = res
        console.log("code",route)
        if(code === 200){

           route.go(-1)
        }

      }
      //点击时间选择器的确认按钮
      const confirmDatePicker = (value) =>{
        state.drinkTime = moment(new Date(value)).format("YYYY-MM-DD HH:mm")
        console.log('点击时间选择器的确认按钮', state.drinkTime,value)
        state.popup1show = false
      }
      //点击时间选择器的取消按钮
      const cancleDatePicker = ()=>{
        state.popup1show = false
      }
      // 计算得出选择饮水量数组
      const getDrinkLineArr = () =>{
        const arr = []
        let max = 1990
        let currentIndexValue = 90
        do {
          currentIndexValue = currentIndexValue + 10
          arr.push(currentIndexValue+"ml")
        }while (currentIndexValue<max)
        return arr
      }
      const formatter = (type, val) =>{
        if (type === 'year')
          return `${val}年`;
        else if (type === 'month')
          return `${val}月`;
        else if( type === 'day')
          return `${val}日`;
        else if( type === 'hour')
          return `${val}时`;
        else if( type === 'minute')
          return `${val}分`;
        return val;
      }

      //选择饮水量监听
      const onChangeDrinkPicker = (picker, values) =>{
        state.drinkValue = picker.substr(0,picker.length-2)
        console.log("选中饮水量--->", state.drinkValue ,picker)
      }
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        columns: [],
        drinkValue: '150', // 选中的最终饮水量
        drinkTime: moment(new Date()).format('YYYY-MM-DD HH:mm'), // 饮水时间
        todo: '',
        popup1show: false,
        selectDate: [100,200,250,300],
        currentDate: new Date(),
        minDate: new Date(2020, 0, 1,0,1),
        maxDate: new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate(),
            new Date().getHours(),new Date().getMinutes()),
      })

      const checked = ref(false);

      const onClickSelect = (index) =>{
        state.drinkValue = state.selectDate[index];
      }

      // 选择事件
      const onClickSelectTime = () => {
        state.popup1show = !state.popup1show
        console.log("是否显示弹出框",state.popup1show)
      }

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        checked,

        onClickSelectTime,
        onClickSelect,
        onChangeDrinkPicker,
        onCommitResult,
        cancleDatePicker,
        confirmDatePicker,
        formatter
      }
    }
  }
</script>

